'use client';

import { useEffect } from 'react';
import { useAuth, useAppInitialization } from '@/shared/store';
import { CircularProgress } from '@mui/material';

interface AppInitializerProps {
  children: React.ReactNode;
}

export const AppInitializer: React.FC<AppInitializerProps> = ({ children }) => {
  const { isAuthenticated, isLoading, isAppReady } = useAuth();
  const { setUserLoaded, initializeRouter } = useAppInitialization();

  useEffect(() => {}, []);

  return (
    <>
      {(!isAppReady || isLoading) && <CircularProgress />}
      {isAppReady && !isLoading && children}
    </>
  );
};
