import { useAuthStore } from '../auth/authStore';
import { useRouterStore } from '../router';
import { useUserStore } from '../user/userStore';

export const useAuth = () => {
  const auth = useAuthStore();
  const user = useUserStore();
  const router = useRouterStore();

  return {
    // Auth state
    isAppReady: router.isInitialized && router.isUserLoaded,
    isAuthenticated: auth.isAuthenticated,
    token: auth.token,
    isLoading: auth.isLoading || user.isLoading,
    error: auth.error || user.error,

    // User state
    user: user.user,

    // Actions
    onLoginRequest: async (credentials: {
      email: string;
      password: string;
    }) => {
      auth.setLoading(true);

      try {
        // Simulate API call
        await new Promise((resolve) => setTimeout(resolve, 1000));

        if (credentials.email && credentials.password) {
          const mockUser = {
            id: '1',
            name: 'John Doe',
            email: credentials.email,
            role: 'user' as const,
            isActive: true,
            createdAt: new Date().toISOString(),
            updatedAt: new Date().toISOString(),
          };

          user.setUser(mockUser);
          localStorage.setItem('auth-token', 'dummy-token');

          router.setInitialized(true);
          router.setUserLoaded(true);
          auth.login('dummy-token');
        } else {
          throw new Error('Invalid credentials');
        }
      } catch (err) {
        auth.setError(err instanceof Error ? err.message : 'Login failed');
      } finally {
        auth.setLoading(false);
      }
    },

    logout: () => {
      auth.logout();
      user.clearUser();
      localStorage.removeItem('auth-token');
      localStorage.removeItem('user-data');
    },

    setUser: user.setUser,
    updateUser: user.updateUser,
    setLoading: (loading: boolean) => {
      auth.setLoading(loading);
      user.setLoading(loading);
    },
    setError: (error: string | null) => {
      auth.setError(error);
      user.setError(error);
    },
  };
};
